_table_header(tournament-theme-5, $tournament_5);\n    .LeaderboardView-buttons {\n        @include flexRow;\n        justify-content: space-between;\n        margin-top: 16px;\n    }\n    .LeaderboardView-content {\n        margin-top: 20px;\n        .tableTitleContainer {\n            border-radius: 12px 12px 0px 0px;\n            .tableTitle {\n                text-align: center;\n                margin: 0 auto;\n                padding: 8px 16px;\n            }\n        }\n        .MuiTableContainer-root {\n            background-color: rgba(255,255,255, 0.95);\n        }\n        .LigaBet-CustomTable {\n            border-radius: 0 0 12px 12px;\n            .MuiTable-root  {\n                border-collapse: separate;\n            }\n            .tableHeaderCell {\n                padding: 8px 16px;\n                font-size: 16px;\n                font-weight: 700;\n            }\n            .LigaBet-CustomTableRow {\n                @include themable_prize_row(prize-theme-1, $prize_1);\n                @include themable_prize_row(prize-theme-2, $prize_2);\n                @include themable_prize_row(prize-theme-3, $prize_3);\n                @include themable_prize_row(prize-theme-4, $prize_4);\n                @include themable_prize_row(prize-theme-5, $prize_5);\n                @include themable_prize_row(prize-theme-6, $prize_6);\n                cursor: pointer;\n                &.currentUtl {\n                    .MuiTableCell-root {\n                        &:first-of-type {\n                            border-right: 6px solid $primary-main;\n                            .rankChange {\n                                margin-right: -2px;\n                            }\n                        }\n                        &.nameCell {\n                            font-weight: 700;\n                        }\n                    }\n                }\n            }\n            .MuiTableCell-root {\n                font-size: 16px;\n                border-bottom-color: rgba(0,0,0,0.1);\n                &.rankChangeCell {\n                    width: 0px;\n                    padding-left: 0px;\n                    padding-right: 8px;\n                }\n                &.rankCell {\n                    width: 0px;\n                    padding-right: 8px;\n                }\n                &.scoreCell {\n                    width: 0px;\n                }\n            }\n        }\n\n        .scoreCell-container {\n            display: flex;\n            align-items: center;\n            justify-content: space-between;\n            .scoreCell-total {\n                font-size: 18px;\n            }\n            .scoreCell-added {\n                padding: 3px 4px;\n                background-color: green;\n                margin-right: 8px;\n                color: #fff;\n                border-radius: 12px;\n                line-height: 1;\n                font-size: 12px;\n                margin-top: -1px;\n                box-shadow: $medium-shadow;\n                text-shadow: none;\n                @include flexRow;\n                > span {\n                    margin-left: -1px;\n                    padding-right: 2px;\n                }\n                > .scoreCell-addIcon {\n                    font-size: 16px;\n                }\n            }\n        }\n        .rankChange {\n            @include flexRow;\n            box-shadow: $medium-shadow;\n            border-radius: 50%;    display: flex;\n            padding-right: 6px;\n            position: relative;\n            display: flex;\n            align-items: center;\n            border-radius: 20px;\n            margin-top: -3px;\n            background-color: #eee;\n            margin-right: 4px;\n            text-shadow: none;\n            height: 22px;\n            .rankChange-value {\n                font-size: 14px;\n                line-height: 1;\n                color: #000;\n                margin-left: -2px;\n            }\n            .rankChange-direction {\n                color: $success-main;\n                transform: rotate(180deg);\n            }\n            &.isNegative {\n                .rankChange-direction {\n                    color: $error-main;\n                    transform: rotate(0deg);\n                }\n            }\n            \n        }\n    }\n}\n\n.LB-NameWithWinnerFlag {\n    @include flexRow;\n    height: 24px;\n    margin-top: -4px;\n    .NameWithWinnerFlag-name {\n        margin-right: 8px;\n        margin-bottom: -1px;\n    }\n    .NameWithWinnerFlag-noWinner {\n        width: 32px;\n        height: 32px;\n    }\n}\n\n\n.LB-ExpanededContestantView {\n    .LB-SpecialBetsTable, .LB-GameBetsTable, .LB-GroupRankBetsTable {\n        .MuiTableContainer-root {\n            background-color: rgb(255 255 255 / 95%);\n            border-radius: 12px;\n            @media (max-width: 600px) {\n                .MuiTableCell-root {\n                    padding: 8px\n                }\n            }\n            .MuiTableCell-root {\n                &.alignToTop {\n                    vertical-align: top;\n                }\n                &.scoreCell {\n                    font-size: 18px;\n                    text-align: center;\n                }\n            }\n        }\n    }\n    @media (max-width: 500px) {\n        .MuiTableContainer-root {\n            width: calc(100% + 16px);\n            margin-right: -8px;\n        }\n    }\n    .hisBetsLink {\n        margin-bottom: 12px;\n        @include centerContainer;\n        .MuiLink-root {\n            cursor: pointer;\n        }\n    }\n}","@import './shadows';\n\n\n@mixin centerContainer {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n@mixin flexRow {\n    display: flex;\n    align-items: center;\n}\n\n@mixin fullView {\n    height: 100%;\n    width: 100%;\n}\n\n@mixin floatingFrame {\n    box-shadow: $basic-shadow;\n    background-color: rgba(255,255,255, 0.95);\n}\n\n@mixin headerText {